import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Checkbox from "@mui/material/Checkbox";

const dispatchedEvents = [];

const SrsMuiCheckbox = function (props, ref) {
  return <Checkbox {...props} theme={props.muiTheme}/>;
};

const styles = `
  /* Custom styles for the MuiCheckbox component */
`;

const props = [
  {
    name: "checked",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "inputProps",
    defaultValue: {},
    type: "object",
  },
  {
    name: "indeterminate",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Checkbox if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiCheckbox),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
